#kick_id{
  animation: fill_anim 0.5s ease forwards 3.3s;
}

.kickback path:nth-child(1){
  stroke-dasharray: 666;
  stroke-dashoffset: 666;
  animation: line-anim 2s ease forwards;
}

.kickback path:nth-child(2){
  stroke-dasharray: 303;
  stroke-dashoffset: 303;
  animation: line-anim 2s ease forwards 0.3s;
}

.kickback path:nth-child(3){
  stroke-dasharray: 472;
  stroke-dashoffset: 472;
  animation: line-anim 2s ease forwards 0.6s;
}

.kickback path:nth-child(4){
  stroke-dasharray: 666;
  stroke-dashoffset: 666;
  animation: line-anim 2s ease forwards 0.9s;
}

.kickback path:nth-child(5){
  stroke-dasharray: 652;
  stroke-dashoffset: 652;
  animation: line-anim 2s ease forwards 1.2s;

}

.kickback path:nth-child(6){
  stroke-dasharray: 563;
  stroke-dashoffset: 563;
  animation: line-anim 2s ease forwards 1.5s;
}

.kickback path:nth-child(7){
  stroke-dasharray: 472;
  stroke-dashoffset: 472;
  animation: line-anim 2s ease forwards 1.8s;
}

.kickback path:nth-child(8){
  stroke-dasharray: 666;
  stroke-dashoffset: 666;
  animation: line-anim 2s ease forwards 2.1s;
}

@keyframes line-anim{
  to{
    stroke-dashoffset: 0;
  }
}

@keyframes fill_anim{
  from{
    fill: transparent;
  }

  to{
    fill: white;
  }
}