@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Serif:ital,wght@1,100&display=swap);

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#kick_id{
  -webkit-animation: fill_anim 0.5s ease forwards 3.3s;
          animation: fill_anim 0.5s ease forwards 3.3s;
}

.kickback path:nth-child(1){
  stroke-dasharray: 666;
  stroke-dashoffset: 666;
  -webkit-animation: line-anim 2s ease forwards;
          animation: line-anim 2s ease forwards;
}

.kickback path:nth-child(2){
  stroke-dasharray: 303;
  stroke-dashoffset: 303;
  -webkit-animation: line-anim 2s ease forwards 0.3s;
          animation: line-anim 2s ease forwards 0.3s;
}

.kickback path:nth-child(3){
  stroke-dasharray: 472;
  stroke-dashoffset: 472;
  -webkit-animation: line-anim 2s ease forwards 0.6s;
          animation: line-anim 2s ease forwards 0.6s;
}

.kickback path:nth-child(4){
  stroke-dasharray: 666;
  stroke-dashoffset: 666;
  -webkit-animation: line-anim 2s ease forwards 0.9s;
          animation: line-anim 2s ease forwards 0.9s;
}

.kickback path:nth-child(5){
  stroke-dasharray: 652;
  stroke-dashoffset: 652;
  -webkit-animation: line-anim 2s ease forwards 1.2s;
          animation: line-anim 2s ease forwards 1.2s;

}

.kickback path:nth-child(6){
  stroke-dasharray: 563;
  stroke-dashoffset: 563;
  -webkit-animation: line-anim 2s ease forwards 1.5s;
          animation: line-anim 2s ease forwards 1.5s;
}

.kickback path:nth-child(7){
  stroke-dasharray: 472;
  stroke-dashoffset: 472;
  -webkit-animation: line-anim 2s ease forwards 1.8s;
          animation: line-anim 2s ease forwards 1.8s;
}

.kickback path:nth-child(8){
  stroke-dasharray: 666;
  stroke-dashoffset: 666;
  -webkit-animation: line-anim 2s ease forwards 2.1s;
          animation: line-anim 2s ease forwards 2.1s;
}

@-webkit-keyframes line-anim{
  to{
    stroke-dashoffset: 0;
  }
}

@keyframes line-anim{
  to{
    stroke-dashoffset: 0;
  }
}

@-webkit-keyframes fill_anim{
  from{
    fill: transparent;
  }

  to{
    fill: white;
  }
}

@keyframes fill_anim{
  from{
    fill: transparent;
  }

  to{
    fill: white;
  }
}
body{
  background-color: black;
}

.kickback{
  width: 100%;
  color: #FFFFF0;
  text-align: center;
}

.title{
  padding-top: 40vh;
  font-size: 5vh;
  font-family: 'IBM Plex Serif', serif;
  text-decoration: underline;
  padding-bottom: 40vh;
}

.scroll-button{
  font-size: 3vh;
}

.scroll-button:hover{
  cursor: pointer;
}

.new-video-title{
  font-size: 3vh;
  font-family: 'IBM Plex Serif', serif;
  padding-bottom: 20px;
}

.new-video{
  padding-top: 45vh;
  padding-bottom: 75px;
}

.youtube-player {
  width:  90vw;
  height: 50.625vw;
  line-height: 0;
}

.new-songs{
  padding-bottom: 10vh;
}

.soundcloud{
  display: inline-block;
  padding: 5vw;
  width:80vw;
}

.song-title{
  padding: 2vw;
  font-size: 3vh;
  font-family: 'IBM Plex Serif', serif;
}

.clothing{
  font-size: 4vh;
  margin-bottom: 15vh;
  background-color: #FFFFF0;
  font-family: 'IBM Plex Serif', serif;
  height: 6vh;
  padding-top: 1vh;
  margin-left: 15vw;
  margin-right: 15vw;
}

.socials{
  padding-bottom: 10vh;
  font-size: 3vh;

}

.insta{
  padding: 1vh;
  margin-bottom: 3vh;
}

@media only screen and (min-width: 601px) {
  .youtube-player{
    width: 45vw;
    height: 25.3125vw;
    margin-left: 2vw;
    margin-right: 2vw;
  }

  .soundcloud{
    display: inline-block;
    padding: 5vw;
    width:35vw;
  }

  .clothing{
    font-size: 4vh;
    margin-bottom: 15vh;
    background-color: #FFFFF0;
    font-family: 'IBM Plex Serif', serif;
    height: 6vh;
    padding-top: 1vh;
    margin-left: 30vw;
    margin-right: 30vw;
  }
}

.insta > a:hover, .insta > a:visited, .insta > a:link, .insta > a:active
{
  text-decoration: underline;
  color: #FFFFF0;
}

.clothing > a:hover, .clothing > a:visited, .clothing > a:link, .clothing > a:active
{
  text-decoration: underline;
  color: black;
}

